const Mock = require('mockjs')

// 前端笔记
Mock.mock('/list', 'get', {
  type: 'get',
  'code':true,
  'total':30,
  'list|5': [{
    'id|+1': 1,
    'title|1': '@ctitle(15, 30)',
    'details|1': '@cparagraph(15, 100)',
    'childrenname|5-8': Mock.Random.cname(),    
    'adviser|6-8': Mock.Random.cname(),
    'strategy|6-7': Mock.Random.cname(),
    'parentfund|5-7': Mock.Random.cname(),
    'scale|15000-35000.1-2':1000000000,
    'state|3-5': Mock.Random.cname(),
    'new': Mock.Random.integer(0, 1),   
    'name': Mock.Random.cname(),
    'addr': Mock.mock('@county(true)'),
    'age|18-60': 1,
    'birth': Mock.Random.date(),
    'sex': Mock.Random.integer(0, 1)
  }]
})


