<template>
	<section>
		<el-row class="list-title">
			<el-col :span="12" class="breadcrumb-container">
					<strong class="title">子基金</strong>
			</el-col>
			<el-col :span="2" :offset="10" style="text-align: right;"><i v-if="tableVisible" v-on:click="tableVisible = !tableVisible" class="el-icon-arrow-down pointer"></i><i v-on:click="tableVisible = !tableVisible" v-if="!tableVisible" class="el-icon-arrow-up pointer"></i>
			</el-col>	
		</el-row>
		
		<el-collapse-transition>
		<div v-show="tableVisible">	
			<!--工具条-->
			<el-row>
				<el-col :span="24" class="toolbar margin-top">
					<el-form :inline="true" :model="filters">
						<el-form-item label="子基金名称：" >
							<el-input v-model="filters.children" placeholder="子基金名称"></el-input>
						</el-form-item>
						<el-form-item label="母基金名称：" >
							<el-input v-model="filters.mother" placeholder="母基金名称"></el-input>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" v-on:click="getUsers">查询</el-button>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="handleAdd">新增</el-button>
						</el-form-item>
					</el-form>
				</el-col>
			</el-row>

			<!--列表-->
			<el-table :data="users" border highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
				<el-table-column type="index" label="序号" width="80">
				</el-table-column>
				<el-table-column prop="childrenname" label="子基金名称" min-width="180">
				</el-table-column>
				<el-table-column prop="adviser" label="投资顾问" min-width="180">
				</el-table-column>
				<el-table-column prop="strategy" label="策略类型" min-width="200">
				</el-table-column>
				<el-table-column prop="parentfund" label="母基金名称" min-width="200">
				</el-table-column>
				<el-table-column prop="scale" label="(拟)投放规模" min-width="110">
				</el-table-column>
				<el-table-column prop="new" label="是否新产品" min-width="90" :formatter='formatstr'>
				</el-table-column>
				<el-table-column prop="state" label="产品状态" min-width="150">
				</el-table-column>
				<el-table-column label="操作" min-width="90">
					<template scope="scope">
						<el-button size="small" @click="handleEdit(scope.$index, scope.row)">状态更新</el-button>
						<!-- <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button> -->
					</template>				
				</el-table-column>
			</el-table>

			<!--工具条-->
			<el-row >
				<el-col :span="24" class="toolbar">
					<!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button> -->
					<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="5" :total="total" style="float:right;">
					</el-pagination>
				</el-col>
			</el-row>			
			</div>
		</el-collapse-transition>	

		<div class="content">
			<el-row class="head">
				<el-col :span="24">
					<el-tabs v-model="activeName" @tab-click="handleClick">
						<el-tab-pane label="产品概要" name="first">产品概要</el-tab-pane>
						<el-tab-pane label="资金申赎" name="second">
							<redemption></redemption>
						</el-tab-pane>
					</el-tabs>
				</el-col>
			</el-row>				
		</div>

		<!--编辑界面-->
		<el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="姓名" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="性别">
					<el-radio-group v-model="editForm.sex">
						<el-radio class="radio" :label="1">男</el-radio>
						<el-radio class="radio" :label="0">女</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="年龄">
					<el-input-number v-model="editForm.age" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="生日">
					<el-date-picker type="date" placeholder="选择日期" v-model="editForm.birth"></el-date-picker>
				</el-form-item>
				<el-form-item label="地址">
					<el-input type="textarea" v-model="editForm.addr"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<!--新增界面-->
		<el-dialog title="新增" :visible.sync="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="姓名" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="性别">
					<el-radio-group v-model="addForm.sex">
						<el-radio class="radio" :label="1">男</el-radio>
						<el-radio class="radio" :label="0">女</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="年龄">
					<el-input-number v-model="addForm.age" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="生日">
					<el-date-picker type="date" placeholder="选择日期" v-model="addForm.birth"></el-date-picker>
				</el-form-item>
				<el-form-item label="地址">
					<el-input type="textarea" v-model="addForm.addr"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	import redemption  from "./sub-funds-detail.vue";
	export default {
		components:{redemption},
		data() {
			return {
				tableVisible:true,
				activeName: 'first',
				filters: {
					children: '',
					mother:''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
					id: 0,
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				},

				addFormVisible: false,//新增界面是否显示
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//新增界面数据
				addForm: {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				}

			}
		},
		methods: {
			handleClick(tab, event) {
        console.log(tab, event);
      },
			//性别显示转换
			formatstr: function (row, column) {
				return row.new == 1 ? '老产品' : row.new == 0 ? '新产品' : '未知';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取用户列表
			getUsers(val) {
				let para = {
					page: this.page,
					children: this.filters.children,
					mother:this.filters.mother
				};
				this.listLoading = true;
				//NProgress.start();
				// getUserListPage(para).then((res) => {
				// 	this.total = res.data.total;
				// 	this.users = res.data.users;
				// 	this.listLoading = false;
				// 	//NProgress.done();
				// });
				let that = this;
				this.$http({
		          method: 'get',
		          url: '/list',
		          data: para
		        }).then(function (result) {
		          let data = result.data;
			          // if (!data.code) {
			          //   window.location.reload()
			          // } else {
			          //   _self.loginErrMsg = data.msg
			          // }
						that.total = data.total;
						that.users = data.list;
						that.listLoading = false;

		        }).catch(function (error) {
		          console.log(error)
		        });

			},
			//删除
			handleDel: function (index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			//显示编辑界面
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//显示新增界面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				};
			},
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//新增
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style scoped>

</style>


